'use client';

import { useState } from 'react';
import { X, Table, Users, TrendingUp, Settings } from 'lucide-react';
import { Button } from '@/components/ui/button';

export function ScrollButtons() {
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const [isDark, setIsDark] = useState(true);

  const toggleTheme = () => {
    setIsDark(!isDark);
    document.documentElement.classList.toggle('dark');
  };

  return (
    <>
      {/* Theme Toggle - Top Left */}
      <div className="fixed top-6 left-6 z-50">
        <Button
          size="icon"
          className="h-16 w-16 rounded-full bg-gradient-to-r from-cyan-500 to-blue-500 hover:from-cyan-600 hover:to-blue-600 shadow-lg flex items-center justify-center"
          onClick={toggleTheme}
        >
          <div className="w-8 h-1 bg-white rounded-full" />
        </Button>
      </div>

      {/* Menu Toggle - Top Right */}
      <div className="fixed top-6 right-6 z-50">
        <Button
          size="icon"
          className="h-16 w-16 rounded-full bg-gradient-to-r from-purple-500 to-pink-500 hover:from-purple-600 hover:to-pink-600 shadow-lg flex items-center justify-center"
          onClick={() => setIsMenuOpen(!isMenuOpen)}
        >
          {isMenuOpen ? (
            <X className="h-7 w-7 text-white" />
          ) : (
            <div className="flex flex-col gap-2 items-center">
              <div className="w-8 h-1 bg-white rounded-full" />
              <div className="w-8 h-1 bg-white rounded-full" />
              <div className="w-8 h-1 bg-white rounded-full" />
            </div>
          )}
        </Button>
      </div>

      {/* Side Menu */}
      {isMenuOpen && (
        <>
          <div 
            className="fixed inset-0 bg-black/50 z-40"
            onClick={() => setIsMenuOpen(false)}
          />
          <div className="fixed top-0 right-0 h-full w-80 bg-white dark:bg-gray-800 shadow-2xl z-50 p-6">
            <div className="flex justify-between items-center mb-8">
              <h2 className="text-2xl font-bold text-gray-900 dark:text-white">Menu</h2>
              <Button size="icon" variant="ghost" onClick={() => setIsMenuOpen(false)}>
                <X className="h-6 w-6" />
              </Button>
            </div>

            <div className="space-y-4">
              <a href="/tables" className="flex items-center gap-3 px-4 py-3 rounded-lg bg-purple-100 dark:bg-purple-900/30 text-purple-900 dark:text-purple-300 font-semibold hover:bg-purple-200 dark:hover:bg-purple-900/50 transition">
                <Table className="h-5 w-5" />
                <span>My Tables</span>
              </a>
              <a href="/referrals" className="flex items-center gap-3 px-4 py-3 rounded-lg bg-gray-100 dark:bg-gray-700 text-gray-900 dark:text-gray-300 font-semibold hover:bg-gray-200 dark:hover:bg-gray-600 transition">
                <Users className="h-5 w-5" />
                <span>Referrals</span>
              </a>
              <a href="/stats" className="flex items-center gap-3 px-4 py-3 rounded-lg bg-gray-100 dark:bg-gray-700 text-gray-900 dark:text-gray-300 font-semibold hover:bg-gray-200 dark:hover:bg-gray-600 transition">
                <TrendingUp className="h-5 w-5" />
                <span>Statistics</span>
              </a>
              <a href="/settings" className="flex items-center gap-3 px-4 py-3 rounded-lg bg-gray-100 dark:bg-gray-700 text-gray-900 dark:text-gray-300 font-semibold hover:bg-gray-200 dark:hover:bg-gray-600 transition">
                <Settings className="h-5 w-5" />
                <span>Settings</span>
              </a>
            </div>
          </div>
        </>
      )}
    </>
  );
}
